import React from "react";
import Collapsible from "react-collapsible";

export default function BillItem(bills) {
    return (
        <div>
            {bills.groupedData.map(((item, index) =>
                <ul key={index} style={{backgroundColor: index % 2 === 0 ? '#f0f0f0' : '#ffffff', paddingRight: '10px'}}>
                    <div style={{height: '50px'}}>
                        <div style={{float: 'left', textAlign: 'center', lineHeight: '50px'}}>{item.name}</div>
                        <div style={{float: 'right', textAlign: 'center'}}>
                            <span style={{float: 'right'}}>${item.amount}</span>
                            <br />
                            <span style={{float: 'right', fontSize: '10px'}}>Due Date: {item.date}</span>
                        </div>
                    </div>
                </ul>
            ))}
        </div>
    );
}