import React, { useState } from 'react';
import Select from "react-select";

const accountTypes = [
    { value: "Savings", label: "Savings Account" },
    { value: "Checkings", label: "Checkings Account" }
];

const banks = [
    { value: "JPMC", label: "Chase" },
    { value: "BOFA", label: "Bank of America" },
    { value: "Capital One", label: "Capital One" },
    { value: "WellsFargo", label: "Wells Fargo N.A." }
];

const BankAccountForm = () => {
    const [userChoice, setUserChoice] = useState("");

    const [formData, setFormData] = useState({
        accountHolderName: '',
        accountNumber: '',
        bankName: '',
        accountType: '',
        nickName: '',
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission logic here, such as sending data to a server
        console.log('Form submitted:', formData);
    };

    const CustomStyle = {
        option: (base, state) => ({
            ...base,
            textAlign: 'left',
        })
    }

    return (
        <div style={styles.container}>
            <h2 style={{backgroundColor: 'paleturquoise', lineHeight: '50px', textAlign: 'center'}}>Add Bank Account</h2>
            <form onSubmit={handleSubmit} style={styles.form}>
                <div style={styles.formGroup}>
                    <label style={styles.label}>Select Bank:</label>
                    <Select
                        // styles={{
                        //     control: (baseStyles, state) => ({
                        //         ...baseStyles,
                        //         textAlign: 'left',
                        //     })
                        // }}
                        styles={CustomStyle}
                        isClearable={false}
                        className="react-select"
                        classNamePrefix="select"
                        options={banks}
                        onChange={(choice) => setUserChoice(choice)}
                    />
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}>Select Account Type:</label>
                    <Select
                        isClearable={false}
                        className="react-select"
                        classNamePrefix="select"
                        options={accountTypes}
                        onChange={(choice) => setUserChoice(choice)}
                    />
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}>Account Holder Name:</label>
                    <input
                        type="text"
                        name="accountHolderName"
                        value={formData.accountHolderName}
                        onChange={handleChange}
                        required
                        style={styles.input}
                    />
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}>Account Number:</label>
                    <input
                        type="text"
                        name="accountNumber"
                        value={formData.accountNumber}
                        onChange={handleChange}
                        required
                        style={styles.input}
                    />
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}>Account Nick Name:</label>
                    <input
                        type="text"
                        name="nickName"
                        value={formData.nickName}
                        onChange={handleChange}
                        style={styles.input}
                    />
                </div>
                <button type="submit" style={styles.button}>Add Account</button>
            </form>
        </div>
    );
};

const styles = {
    container: {
        maxWidth: '500px',
        // margin: '0 auto',
    },
    form: {
        display: 'flex',
        flexDirection: 'column',
        padding: '20px',
    },
    formGroup: {
        marginBottom: '15px',
        // display: 'flex',
        // alignItems: 'center'
    },
    label: {
        marginBottom: '5px',
        fontWeight: 'bold',
        float: 'left',
    },
    input: {
        width: '100%',
        padding: '8px',
        borderRadius: '5px',
        border: '1px solid #ccc',
    },
    button: {
        padding: '10px 15px',
        backgroundColor: '#007BFF',
        color: '#fff',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
    },
};

export default BankAccountForm;
