import React, { useState, useEffect } from "react";
import HomeScreen from "./HomeScreen";
import SettingsScreen from "./SettingsScreen";
import {SafeAreaView, StyleSheet} from "react-native";
import Taskbar from "./TaskBar";

const bill = [
    {name: 'Power Bill', date: '1-2-2024', amount: 50.45},
    {name: 'Water Bill', date: '1-4-2024', amount: 30},
    {name: 'Bofa Credit Card', date: '1-2-2024', amount: 500},
    {name: 'Wells fargo Credit Card', date: '1-6-2024', amount: 445},
];

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
});

export default function LandingPage() {
    const [currentScreen, setCurrentScreen] = useState('Home');

    // useEffect(() => {
    //     fetch('http://localhost:8080/bills/all')
    //         .then(response => response.json())
    //         .then(json => console.log("test"))
    //         .catch(error => console.error(error));
    // }, []);

    const renderScreen = () => {
        switch (currentScreen) {
            case 'Home':
                return <HomeScreen bill={bill}/>;
            case 'Accounts':
                return <SettingsScreen />;
            case 'Merchants':
                return <SettingsScreen />;
            case 'Profile':
                return <SettingsScreen />;
            default:
                return <HomeScreen />;
        }
    };

    return (
        <SafeAreaView style={styles.container}>
            {renderScreen()}
            <Taskbar navigate={setCurrentScreen} />
        </SafeAreaView>
    );
}